import React, { useState, useEffect } from 'react'
import ComPagination from '@/components/comPagination/ComPagination.jsx'
import { Input, Table } from 'antd';
import moment from 'moment';
import { debounce } from '../../utils/util';
import './Log.less'

import { transaction } from "../../api/finance"
import { getApiLog, getLog } from "../../api/account"
import { getCommon } from "../../api/common"

export default function Log() {



  // 分页相关
  const [total, setTotal] = useState(200)
  const [params, setParams] = useState({
    keywords: '',
    page: 1,
    limit: 20,
    orderby: 'id',
    sort: 'desc'
  })

  // 标题激活id   0:操作日志 1:api日志
  const [activeId, setActiveId] = useState(0)

  useEffect(() => {
    activeId === 0 ?
      getLogList()
      :
      getApiLogList()
  }, [params, activeId]);



  // 表格相关
  const [isTableLoading, setIsTableLoading] = useState(false)
  const [datasource, setDatasource] = useState([])
  const changePage = (page, limit) => {
    setParams(params => {
      return { ...params, page, limit }
    })
  }

  const columns = [
    {
      title: 'ID',
      width: 60,
      align: "left",
      dataIndex: 'id',
    },
    {
      title: '描述',
      dataIndex: 'description',
      ellipsis: true,
      key: 'description',
      width: 400
    },
    {
      title: '创建时间',
      dataIndex: 'create_time',
      key: 'create_time',
      width: 170,
      render: (create_time) => (
        <>
          {moment(create_time * 1000).format("YYYY-MM-DD HH:mm")}
        </>
      )
    },
    {
      title: 'IP',
      dataIndex: 'ip',
      key: 'ip',
      width: 100
    },
  ]

  // 查询api日志
  const searchLog = (event) => {
    let keywords = event.target.value
    let page = 1
    setParams(params => {
      return { ...params, page, keywords }
    })
  }


  // 获取操作日志
  const getLogList = () => {
    // 表格加载
    setIsTableLoading(true)
    getLog({ ...params, type: "system" }).then(res => {
      if (res.data.status === 200) {
        const count = res.data.data.count;
        setTotal(() => {
          return count
        })
        let list = res.data.data.list

        setDatasource(list)
      }
      setIsTableLoading(false)
    })
  }

  // 获取api日志
  const getApiLogList = () => {
    // 表格加载
    setIsTableLoading(true)
    getLog({ ...params, type: "api" }).then(res => {
      if (res.data.status === 200) {
        const count = res.data.data.count;
        setTotal(() => {
          return count
        })
        let list = res.data.data.list
        setDatasource(list)
      }
      setIsTableLoading(false)
    })
  }


  return (
    <div className='com-card log'>
      <div className="card-top">
        <span className='title'>
          <p className='title-l' onClick={() => setActiveId(0)} style={activeId === 0 ? { color: "#040E34" } : {}}>操作日志</p>
          <p className='title-r' onClick={() => setActiveId(1)} style={activeId === 1 ? { color: "#040E34" } : {}}>API日志</p>
        </span>
        <Input prefix={<img src={require('../../assets/images/search.png')} />}
          onChange={debounce(searchLog, 500)}
          className='top-search-input'
          placeholder="请输入" />
      </div>
      <div className="card-main">
        <div className="main_table">
          <Table columns={columns}
            dataSource={datasource}
            pagination={false}
            loading={isTableLoading}
            rowKey={record => record.id}
            scroll={datasource.length !== 0 ? {
              x: '100%',
              scrollToFirstRowOnChange: true
            } : null}
          />
        </div>
      </div>
      {/* 分页模块 */}
      <ComPagination total={total} page={params} changePage={changePage}></ComPagination>

    </div>
  )
}
